/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.header-light {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  height: 4.25rem;
  padding-top: 2rem;
  margin: auto;
}
.header-light nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1320px;
}
.header-light .menu-top-nav-menu-container ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding-left: 0;
  align-items: center;
}
.header-light .menu-top-nav-menu-container ul .dropdown-toggle::after {
  margin-left: -18px;
  margin-top: 10px;
  margin-right: 20px;
  color: black;
  font-size: 10px;
}
.header-light .menu-top-nav-menu-container ul li {
  position: relative;
}
@media screen and (min-width: 769px) {
  .header-light .menu-top-nav-menu-container ul li.menu-item-has-children:hover .sub-menu {
    display: flex;
    position: absolute;
    flex-direction: column;
    background-color: blue;
    padding: 20px;
    top: 100%;
    left: -28px;
    width: 205px;
  }
  .header-light .menu-top-nav-menu-container ul li.menu-item-has-children:hover .sub-menu li {
    margin-bottom: 20px;
  }
}
.header-light .menu-top-nav-menu-container ul li a {
  text-decoration: none;
  color: black;
  font-size: 0.8rem;
  font-weight: 700;
  margin-right: 20px;
  position: relative;
}
.header-light .menu-top-nav-menu-container ul li a:after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: black;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}
.header-light .menu-top-nav-menu-container ul li a:hover:after {
  width: 100%;
  left: 0;
}
.header-light .menu-top-nav-menu-container ul .current-menu-item li a {
  color: red;
}
.header-light .menu-top-nav-menu-container ul .sub-menu {
  display: none;
}
@media screen and (max-width: 768px) {
  .header-light .menu-top-nav-menu-container {
    position: absolute;
    top: 0px;
    display: flex;
    right: -41px;
    flex-direction: row;
    width: 50%;
    background-color: blue;
    height: 100vh;
    transform: translateX(100%);
    transition: all 0.5s ease-in-out;
  }
  .header-light .menu-top-nav-menu-container ul {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 20%;
    justify-content: space-between;
  }
  .header-light .menu-top-nav-menu-container ul .dropdown-toggle::after {
    display: none;
  }
  .header-light .menu-top-nav-menu-container ul li {
    margin-bottom: 16px;
    position: relative;
  }
  .header-light .menu-top-nav-menu-container ul li.menu-item-has-children .sub-menu, .header-light .menu-top-nav-menu-container ul li.menu-item-has-children:hover .sub-menu {
    display: flex;
    margin-top: 10px;
    margin-left: 5px;
    align-items: flex-start;
    flex-direction: column;
    background-color: blue;
  }
  .header-light .menu-top-nav-menu-container ul li.menu-item-has-children .sub-menu li:before, .header-light .menu-top-nav-menu-container ul li.menu-item-has-children:hover .sub-menu li:before {
    content: "-";
    font-size: 20px;
    color: black;
  }
  .header-light .menu-top-nav-menu-container ul li a {
    transition: all 0.5s ease-in-out;
    opacity: 0;
  }
  .header-light .menu-top-nav-menu-container ul#menu-top-nav-menu {
    margin: 20px auto;
  }
}
@media screen and (max-width: 768px) {
  .header-light .menu-top-nav-menu-container {
    width: 60%;
  }
}
.header-light .nav-active {
  transform: translateX(0%);
}
.header-light .nav-active ul li a {
  opacity: 1;
}
.header-light .navbar__logo img {
  height: 40px;
}
.header-light .mobile-menu {
  cursor: pointer;
}
.header-light .mobile-menu div {
  width: 30px;
  height: 3px;
  color: black;
  margin: 7px;
  background-color: black;
  border-radius: 5px;
  transition: all 0.3s;
}
@media screen and (min-width: 768px) {
  .header-light .mobile-menu {
    display: none;
  }
}
.header-light .nav-active .line1 {
  transform: rotate(-45deg) translate(-8px, 10px);
}
.header-light .nav-active .line2 {
  opacity: 0;
}
.header-light .nav-active .line3 {
  transform: rotate(45deg) translate(-4px, -5px);
}

